@import './var.scss';

.daily-specials {
  width: 100%;
  margin-top: 10px;
  margin-bottom: 10px;
  .seoulContainer {
    overflow: hidden;
    // background: red;
    // position: relative;

    padding: 0 5px;
    display: flex;
    flex-direction: column;
    // flex-wrap: wrap;
    // max-height: 600px;

    .left {
      position: relative;
      overflow: hidden;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      height: 340px;
      img {
        width: 100%;
        height: 100%;
        backface-visibility: hidden;
      }
      .content {
        left: 0;
        top: 0;
        right: 0;
        position: absolute;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100%;
        p {
          flex: 1 0 auto;
          display: flex;
          align-items: center;
          color: #fff;
          text-align: center;
          font-size: 20px;
          text-shadow: 0 0 4px rgba(0, 0, 0, 0.72), 0 0 14px rgba(0, 0, 0, 0.45);
        }
        .left-name {
          font-family: $fontAmatic;
          font-weight: 700;
          font-size: 44px;
        }
      }
    }
    .right {
      // position: absolute;
      // left: 50%;
      // transform: translateX(-50%);
      // bottom: 0;
      // height: 160px;
      width: 100%;
      display: flex;
      justify-content: center;
      align-items: flex-end;
      .right-inner {
        display: flex;
        flex-wrap: wrap;
        // padding: 10px;
        padding: 5px;
        width: 100%;
        .right-item {
          flex-basis: 33.33%;
          // padding: 10px;
          padding: 4px;

          &.active {
            // visibility: hidden;
            .right-item-inner:before {
              opacity: 0;
            }
            .right-item-inner {
              .content {
                background: rgba(0, 0, 0, 0.3);
              }
            }
          }

          .right-item-inner {
            filter: brightness(110%);
            width: 100%;
            height: 60px;
            // height: 200px;
            // position: relative;
            cursor: pointer;
            overflow: hidden;
            backface-visibility: hidden;
            border-radius: 5px;
            box-shadow: inset 0 0 10px 2px rgba(0, 0, 0, 0.2);
            &:before {
              position: absolute;
              content: '';
              top: 0;
              left: 0;
              right: 0;
              height: 100%;
              background: linear-gradient(
                to right,
                rgba(0, 0, 0, 0.7),
                rgba(0, 0, 0, 0.3)
              );
              transition: opacity 500ms ease-in-out;
              opacity: 1;
            }
            .content {
              color: #fff;
              position: absolute;
              top: 50%;
              left: 50%;
              transform: translate(-50%, -50%);
              padding: 2px 6px;
              border-radius: 3px;
              text-shadow: 0 0 4px rgba(0, 0, 0, 0.72),
                0 0 14px rgba(0, 0, 0, 0.45);
              transition: background 500ms ease-in-out;

              h5 {
                font-style: italic;
              }
              .everyday {
                color: lighten($lightBlue, 30%);
              }
            }
          }
        }
      }
    }

    @media (min-width: 1024px) {
      height: 100%;
      // max-height: 500px;
      height: 500px;
      flex-direction: row;
      .left,
      .right {
        flex-basis: 50%;
        height: 100%;
      }
      .left {
        border-radius: 5px;
        order: 1;
      }
      .right {
        order: 2;
        // position: static;
        // transform: translateX(0);
        height: auto;
        justify-content: center;
        align-items: center;

        .right-inner {
          padding: 10px;
          .right-item {
            padding: 10px;
            &.active {
              // visibility: visible;
              .right-item-inner:before {
                opacity: 0;
              }
              .right-item-inner {
                .content {
                  background: rgba(0, 0, 0, 0.3);
                }
              }
            }
            .right-item-inner {
              height: 200px;
              &:before {
                background: linear-gradient(
                  to right,
                  rgba(0, 0, 0, 0.7),
                  rgba(0, 0, 0, 0.3)
                );
              }
            }
          }
        }
      }
    }
  }
}
